<template>
  <div class="remote-bottom-style">
    <div class="remote-style">
      <div
        class="tj-main"
        v-loading="phoneCountRes"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        element-loading-text="加载中"
      >
        <div class="title">
          <!--                    <i class="el-icon-phone-outline"></i> -->
          年电话量
        </div>
        <div class="tj-all-count">{{ yearPhonesCount.callVolumeForYear }}</div>
        <div class="kb-count">
          <table cellpadding="0" cellspacing="0" class="kb-table">
            <tr class="title-tr">
              <td>最高月电话量</td>
              <td>日期</td>
            </tr>
            <tr class="value-tr">
              <!--                            <td>{{parseInt(yearPhonesCount.maxCallVolumeForMonth.ccsum).toLocaleString()}}</td>-->
              <td>
                {{ parseInt(yearPhonesCount.maxCallVolumeForMonth.ccsum) }}
              </td>
              <td>
                {{
                  dateTranslate(
                    "mounth",
                    yearPhonesCount.maxCallVolumeForMonth.callday
                  )
                }}
              </td>
            </tr>
            <tr class="title-tr">
              <td>最高日电话量</td>
              <td>日期</td>
            </tr>
            <tr class="value-tr">
              <td>{{ parseInt(yearPhonesCount.maxCallVolumeForDay.ccsum) }}</td>
              <td>
                {{
                  dateTranslate(
                    "date",
                    yearPhonesCount.maxCallVolumeForDay.callday
                  )
                }}
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div
        class="charts-main border-style"
        v-loading="!mounthPhoneRes"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        element-loading-text="加载中"
      >
        <div class="title">月电话量</div>
        <div>
          <countLabel
            v-if="mounthPhoneRes"
            :countData="mounthLabel"
            :countUnit="phonesUnit"
          ></countLabel>
        </div>
        <div class="charts-style">
          <bda-line-charts
            v-if="mounthPhoneRes"
            :chartData="mounthPhoneData"
            :nameXs="mounthNamexs"
          ></bda-line-charts>
        </div>
      </div>
      <div
        class="charts-main border-style"
        v-loading="!dayPhoneRes"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        element-loading-text="加载中"
      >
        <div class="title">日电话量</div>
        <div>
          <countLabel
            v-if="dayPhoneRes"
            :countData="dayLabel"
            :countUnit="phonesUnit"
          ></countLabel>
        </div>
        <div class="charts-style">
          <bda-line-charts
            v-if="dayPhoneRes"
            :chartData="dayPhoneData"
            :nameXs="dayNamexs"
          ></bda-line-charts>
        </div>
      </div>
    </div>
    <div class="remote-style">
      <div
        class="tj-main"
        v-loading="!dayPhoneRes"
        element-loading-background="rgba(0, 0, 0, 0.5)"
        element-loading-text="加载中"
      >
        <div class="title">
          <!--                    <i class="el-icon-document"></i> -->
          年业务量
        </div>
        <div class="tj-all-count">{{ yearBusinessCount.yearCount }}</div>
        <div class="kb-count">
          <table cellpadding="0" cellspacing="0" class="kb-table">
            <tr class="title-tr">
              <td>最高月业务量</td>
              <td>日期</td>
            </tr>
            <tr class="value-tr">
              <td>{{ parseInt(yearBusinessCount.mounthCount.count) }}</td>
              <td>
                {{
                  dateTranslate("mounth", yearBusinessCount.mounthCount.date)
                }}
              </td>
            </tr>
            <tr class="title-tr">
              <td colspan="2">今日抢修类业务</td>
            </tr>
            <tr class="value-tr">
              <td colspan="2">{{ parseInt(yearBusinessCount["抢修类"]) }}</td>
            </tr>
            <tr class="title-tr">
              <td colspan="2">今日非抢修类业务</td>
            </tr>
            <tr class="value-tr">
              <td colspan="2">{{ parseInt(yearBusinessCount["非抢修类"]) }}</td>
            </tr>
          </table>
        </div>
      </div>
      <div class="charts-main border-style">
        <div class="title">月业务量</div>
        <div>
          <countLabel
            v-if="businessMonthRes"
            :countData="businessMonthLabel"
            :countUnit="phonesUnit"
          ></countLabel>
        </div>
        <div class="charts-style3">
          <bda-bar-charts
            v-if="businessMonthRes"
            :chartsConfig="businessMonthData"
            :barType="barType"
            :bar-width="2"
            :changeAxios="true"
            :legendStatus="false"
          ></bda-bar-charts>
        </div>
      </div>
      <div class="charts-main border-style">
        <div class="title">日业务量</div>
        <div>
          <countLabel
            v-if="businessDayRes"
            :countData="businessDayLabel"
            :countUnit="phonesUnit"
          ></countLabel>
        </div>
        <div class="charts-style3">
          <bda-bar-charts
            v-if="businessDayRes"
            :chartsConfig="businessDayData"
            :barType="barType"
            :bar-width="2"
            :changeAxios="true"
            :legendStatus="false"
          ></bda-bar-charts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/remote-work-monitoring';
import qs from 'qs';
import bdaLineCharts from '../../../widgets/bda-line-charts/bda-line-charts';
import countLabel from "@/components/countLabel/countLabel";
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';

export default {
  name: "remote-bottom",
  mixins: [Interface],
  components: { bdaLineCharts, countLabel, bdaBarCharts },
  data() {
    return {
      yearPhonesCount: {
        callVolumeForYear: 0,
        maxCallVolumeForMonth: {
          ccsum: 0,
          callday: ''
        },
        maxCallVolumeForDay: {
          ccsum: 0,
          callday: ''
        }
      },
      yearBusinessCount: {
        yearCount: 0,
        mounthCount: {
          date: '',
          count: 0
        },
        '抢修类': 0,
        '非抢修类': 0
      },
      mounthPhoneRes: false,
      mounthPhoneData: {},
      mounthNamexs: '日',
      dayPhoneRes: false,
      dayPhoneData: {},
      dayNamexs: '时',
      mounthLabel: [],
      dayLabel: [],
      phonesUnit: '',
      businessMonthLabel: [],
      businessDayLabel: [],
      businessMonthData: {},
      businessDayData: {},
      businessMonthRes: false,
      businessDayRes: false,
      barType: 'stack',
      phoneCountRes: true,
      businessCountRes: true

    }
  },
  watch: {},
  computed: {},
  created() {

  },
  mounted() {
    if (this.$parent.dateParams) {
      this.getCallVolumeForYear(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.yearPhonesCount = res;
        this.phoneCountRes = false;
      }).catch(() => {
        this.phoneCountRes = false;
      });
      this.getBusinessVolumeForYear(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.businessCountRes = false;
        this.bussinessForYear(res);
      }).catch(() => {
        this.businessCountRes = false;
      });
      this.getCallVolumeForMonth(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.callVolumeCallback(res, 'month');
      }).catch(() => {
        this.mounthPhoneRes = false;
      });
      this.getCallVolumeForDay(qs.stringify({
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.callVolumeCallback(res, 'day');
      }).catch(() => {
        this.dayPhoneRes = false;
      });
      // codeType
      this.getObjByCodeType(qs.stringify({
        codeType: 'csBusiType'
      })).then(data => {
        this.getBusinessVolumeForMonth(qs.stringify({
          timeStr: this.$parent.dateParams
        })).then(res => {
          this.bussinessCallback(res, data, 'month');
        }).catch(() => {
          this.businessMonthRes = false;
        });
        this.getBusinessVolumeForDay(qs.stringify({
          timeStr: this.$parent.dateParams
        })).then(res => {
          this.bussinessCallback(res, data, 'day');
        }).catch(() => {
          this.businessDayRes = false;
        });
      })
    }


  },
  methods: {
    dateTranslate(type, dateStr) {
      let date = "";
      if (dateStr) {
        let str = ''
        if (dateStr.length === 6) {
          str = dateStr.substring(0, 4) + '/' + dateStr.substring(4, 6) + '/01'
        } else if (dateStr.length === 8) {
          str = dateStr.substring(0, 4) + '/' + dateStr.substring(4, 6) + '/' + dateStr.substring(6, 8)
        }
        if (str) {
          let dqDate = new Date(str);
          if (type === 'mounth') {
            date = (dqDate.getMonth() + 1) + '月'
          } else {
            date = (dqDate.getMonth() + 1) + '月' + dqDate.getDate() + '日'
          }
        }

      }
      return date;
    },
    bussinessForYear(data) {
      if (data) {
        this.yearBusinessCount.yearCount = data.businessVolumeForYear !== undefined ? data.businessVolumeForYear : 0;
        if (data.maxBusinessVolumeForMonth) {
          this.yearBusinessCount.mounthCount = {
            count: data.maxBusinessVolumeForMonth.ccsum !== undefined ? data.maxBusinessVolumeForMonth.ccsum : 0,
            date: data.maxBusinessVolumeForMonth.callday ? data.maxBusinessVolumeForMonth.callday : ''
          }
        }
        if (data.businessVolumeForDay) {
          for (let item of data.businessVolumeForDay) {
            if (item.isrepair === '1') {
              this.yearBusinessCount['抢修类'] = item.ccsum !== undefined ? item.ccsum : 0
            } else {
              this.yearBusinessCount['非抢修类'] = item.ccsum !== undefined ? item.ccsum : 0
            }
          }

        }
      }
    },
    callVolumeDataFilter(data, key1, key2, leg1, leg2, xKey) {
      let xData = [], dataMap = {};
      dataMap[leg1] = [];
      dataMap[leg2] = [];
      let status = '';
      if (data) {
        let beforMonthLen = data[key1] ? data[key1].length : 0;
        let presentMonthLen = data[key2] ? data[key2].length : 0;
        // 判读哪个数据长度较长，则作为x轴的维度信息
        if (beforMonthLen - presentMonthLen >= 0) {
          status = 'before'
        } else {
          status = 'present'
        }
        if (beforMonthLen > 0) {
          for (let item of data[key1]) {
            if (item[xKey]) {
              let disLength = item[xKey].length;
              let name = item[xKey].substring(disLength - 2, disLength);
              item.value = item.ccsum;
              item.name = name;
              if (status === 'before') {
                xData.push(name);
              }
            }
          }
        }
        if (presentMonthLen > 0) {
          for (let item of data[key2]) {
            if (item[xKey]) {
              let disLength = item[xKey].length;
              let name = item[xKey].substring(disLength - 2, disLength);
              item.value = item.ccsum;
              item.name = name;
              if (status === 'present') {
                xData.push(name);
              }
            }
          }
        }
      }
      dataMap[leg1] = data[key1];
      dataMap[leg2] = data[key2];
      return {
        dataMap: dataMap,
        xData: xData
      }
    },
    callVolumeCallback(data, type) {
      let mapData = {}, countMap = [];
      if (data) {
        if (type === 'month') {
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForMonthValue !== undefined ? data.callVolumeForMonthValue : 0,
            showText: '本月电话量'
          });
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForBeforeMonthValue !== undefined ? data.callVolumeForBeforeMonthValue : 0,
            showText: '上月电话量'
          });
          let _filterData = this.callVolumeDataFilter(data, 'callVolumeForBeforeMonthLine', 'callVolumeForMonthLine', '上月', '本月', 'callday');
          mapData = {
            dimension: _filterData.xData,
            dataMap: _filterData.dataMap,
            color: ['#df896d', '#0088d1']
          };
          this.mounthPhoneRes = false;
          this.mounthPhoneData = mapData;
          this.mounthLabel = countMap;
          this.$nextTick(() => {
            this.mounthPhoneRes = true;
          })
        } else {
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForDayValue !== undefined ? data.callVolumeForDayValue : 0,
            showText: '今日电话量'
          });
          countMap.push({
            key: Math.random() + '',
            value: data.callVolumeForBeforeDayValue !== undefined ? data.callVolumeForBeforeDayValue : 0,
            showText: '昨日电话量'
          });
          let _filterData = this.callVolumeDataFilter(data, 'callVolumeForBeforeDayLine', 'callVolumeForDayLine', '昨日', '今日', 'callhour');
          mapData = {
            dimension: _filterData.xData,
            dataMap: _filterData.dataMap,
            color: ['#df896d', '#0088d1']
          };
          this.dayPhoneRes = false;
          this.dayPhoneData = mapData;
          this.dayLabel = countMap;
          this.$nextTick(() => {
            this.dayPhoneRes = true;
          })
        }
      }
    },
    businessDataFilter(data, translateMap, name) {
      let dimension = [], quotaData = [];
      if (data) {
        for (let item of data) {
          if (item.busitypecode) {
            dimension.push(translateMap[item.busitypecode]);
            quotaData.push(item.ccsum)
          }

        }
      }
      let _data = {
        dimension: dimension,
        quotaMap: {}
      };
      _data.quotaMap[name] = quotaData;
      return _data
    },
    bussinessCallback(data, translateMap, type) {
      if (data) {
        if (type === 'month') {
          this.businessMonthLabel = [{
            showText: '本月业务量',
            value: data.businessVolumeForMonthValue !== undefined ? data.businessVolumeForMonthValue : 0,
            key: Math.random() + ''
          }];
          if (data.businessVolumeForMonthLine) {
            this.businessMonthData = this.businessDataFilter(data.businessVolumeForMonthLine, translateMap, '本月业务量');
            this.businessMonthData.colorMap = { '本月业务量': ['#00ccf7', '#00acd0'] }
          }
          this.businessMonthRes = false;
          this.$nextTick(() => {
            this.businessMonthRes = true;
          })

        } else {
          this.businessDayLabel = [{
            showText: '今日业务量',
            value: data.businessVolumeForDayValue !== undefined ? data.businessVolumeForDayValue : 0,
            key: Math.random() + ''
          }];
          if (data.businessVolumeForDayLine) {
            this.businessDayData = this.businessDataFilter(data.businessVolumeForDayLine, translateMap, '今日业务量');
            this.businessDayData.colorMap = { '今日业务量': ['#0deecb', '#00c7a8'] }
          }
          this.businessDayRes = false;
          this.$nextTick(() => {
            this.businessDayRes = true;
          })
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
.remote-bottom-style {
  width: 100%;
  height: calc(100% - 10px);
  position: relative;

  .border-style {
    border: 1px solid #3e7ca6;
    border-radius: 5px;
    box-shadow: #0a65ce 0px 0px 5px inset;
  }

  .remote-style {
    width: calc(50% - 7.5px);
    float: left;
    position: relative;
    height: 100%;

    .tj-main {
      width: 184px;
      height: 100%;
      float: left;
      border: 1px solid #0a65ce;
      border-radius: 5px;
      position: relative;

      .title {
        background: linear-gradient(
          to bottom,
          rgba(15, 146, 229, 0.3),
          #061b3a
        );
        line-height: 40px;
        color: #fff;
        text-align: center;
        /*font-size: 16px;*/
        font-size: 22px;
        letter-spacing: 3px;

        i {
          padding-right: 2px;
          font-size: 22px;
        }
      }

      .tj-all-count {
        color: #0aaef9;
        text-align: center;
        line-height: 50px;
        font-size: 20px;
        font-weight: 600;
      }

      .kb-count {
        width: 100%;
        height: calc(100% - 90px);
        position: relative;

        .kb-table {
          width: 100%;
          height: 100%;

          tr {
            td {
              color: #fff;
            }

            td:first-child {
              border-right: 1px solid #0a65ce;
            }
          }

          tr.title-tr {
            height: 40px;
            text-align: center;
            background: linear-gradient(
              to bottom,
              rgba(15, 146, 229, 0.3),
              #061b3a
            );

            td {
              font-size: 18px;
            }
          }

          tr.value-tr {
            text-align: center;

            td {
              color: #0aaef9;
              font-weight: 600;
              font-size: 18px;
            }
          }
        }
      }
    }

    .charts-main {
      width: calc((100% - 210px) / 2 - 5px);
      height: 100%;
      float: left;
      margin-left: 15px;
      position: relative;

      .title {
        background: linear-gradient(
          to bottom,
          rgba(15, 146, 229, 0.3),
          #061b3a
        );
        color: #fff;
        text-align: center;
        /*font-size: 16px;*/
        font-size: 22px;
        line-height: 38px;
        letter-spacing: 5px;
      }

      .charts-style {
        width: 100%;
        /*height: calc(100% - 70px);*/
        height: calc(100% - 90px);
        position: relative;
      }
      .charts-style3 {
        width: 100%;
        height: calc(100% - 70px);
        position: relative;
      }
    }
  }

  .remote-style:nth-child(2) {
    margin-left: 15px;
  }
}
</style>